import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import { UrlConstants } from 'src/app/_helpers/url-constants';

@Injectable({
  providedIn: 'root'
})
export class OrganizationStructureService {

  constructor(public httpClient : HttpService) { }

  addOrganizationstructure(data, userId): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.addOrganizationstructure + userId ,  'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }

  getOrganizationstructure(data, userId): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.getOrganizationstructure + userId ,  'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }

  updateOrganizationstructure(data, nodeId): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.updateOrganizationstructure + nodeId ,  'PUT').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }

}
