import { HttpClient } from '@angular/common/http';
import { Component, OnInit, AfterViewInit, Input, Output, EventEmitter } from '@angular/core';

import * as go from 'gojs'

const $ = go.GraphObject.make;

@Component({
  selector: 'app-diagram-canvas',
  templateUrl: './diagram-canvas.component.html',
  styleUrls: ['./diagram-canvas.component.scss']
})
export class DiagramCanvasComponent implements OnInit, AfterViewInit {

  public diagram: go.Diagram = null;
  private nodeIdCounter: number = 1;

  @Input() public model: go.Model;
  @Input() public clearFlag : boolean;
  @Output() public nodeClicked = new EventEmitter();

  constructor(private http: HttpClient) {
   }

  ngOnInit(): void {
    if(this.clearFlag === true) {
      this.diagram.clearSelection();
    }
    console.log("ngOnInit", this.model);
    // console.log("ngOnInit", go.Node);
  }
  // Function to generate a unique positive key for new nodes
  generateUniqueKey(): number {
    return ((this.nodeIdCounter++) * (-1));
  }

  public ngAfterViewInit() {
    this.diagram = $(go.Diagram, 'diagram-canvas',
    {
      allowCopy: false,
      allowDelete: true,
      initialAutoScale: go.Diagram.Uniform,
      maxSelectionCount: 1, // users can select only one part at a time
      validCycle: go.Diagram.CycleDestinationTree, // make sure users can only create trees
      //Gets or sets a data object that will be copied and added to the diagram's model each time this tool executes. The default value is null. The value must be non-null for this tool to be able to run. Setting this property does not raise any events.
      "clickCreatingTool.archetypeNodeData": { // allow double-click in background to create a new node   
        // key: this.generateUniqueKey(), // Assign a unique positive ID
        name: "(new person)",
        // title: "",
      },
      "clickCreatingTool.insertPart": function(loc) {  // method override must be function, not =>
        const node = go.ClickCreatingTool.prototype.insertPart.call(this, loc);
        if (node !== null) {
          this.diagram.select(node);
          this.diagram.commandHandler.scrollToPart(node);
          this.diagram.commandHandler.editTextBlock(node.findObject("NAMETB"));
        }
        return node;
      },
        layout:
          $(go.TreeLayout,
            {
              isOngoing: true,
              treeStyle: go.TreeLayout.StyleLastParents,
              arrangement: go.TreeLayout.ArrangementHorizontal,
              // properties for most of the tree:
              angle: 90,
              layerSpacing: 35,
              // properties for the "last parents":
              alternateAngle: 90,
              alternateLayerSpacing: 35,
              alternateAlignment: go.TreeLayout.AlignmentBus,
              alternateNodeSpacing: 20
            }),
        'undoManager.isEnabled': true
      }
    );

    // define the Node template
    this.diagram.nodeTemplate =
      $(go.Node, 'Auto',
        // for sorting, have the Node.text be the data.name
        new go.Binding('text', 'name'),
        // bind the Part.layerName to control the Node's layer depending on whether it isSelected
        new go.Binding('layerName', 'isSelected', function(sel) { return sel ? 'Foreground' : ''; }).ofObject(),
        // define the node's outer shape
        $(go.Shape, 'RoundedRectangle',
          {
            name: 'SHAPE', fill: 'lightblue', stroke: null,
            // set the port properties:
            portId: '', fromLinkable: true, toLinkable: true, cursor: 'pointer'
          },
          new go.Binding('fill', '', function(node) {
            // modify the fill based on the tree depth level
            const levelColors = ['#fff'];
            let color = node.findObject('SHAPE').fill;
            const dia: go.Diagram = node.diagram;
            if (dia && dia.layout.network) {
              dia.layout.network.vertexes.each(function(v: go.TreeVertex) {
                if (v.node && v.node.key === node.data.key) {
                  const level: number = v.level % (levelColors.length);
                  color = levelColors[level];
                }
              });
            }
            return color;
          }).ofObject()
        ),
        $(go.Panel, 'Vertical',
          $(go.Picture,
            {
              name: 'Picture',
              desiredSize: new go.Size(60, 60),
              alignment: new go.Spot(0.5, 0), // Align at top center
              margin: new go.Margin(10, 0, 5, 0), // Add margin (top, right, bottom, left)
            },
            new go.Binding('source', 'key', function(key) {
              if (key < 0 || key > 16) return ''; // There are only 16 images on the server
              return 'assets/img/dashboard-party-profiles/PIC' + key + '.jpg';
            })
          ),
          // define the panel where the text will appear
          $(go.Panel, 'Table',
            {
              maxSize: new go.Size(200, 999),
              margin: new go.Margin(10, 10, 10, 10),
              defaultAlignment: go.Spot.Left
            },
            $(go.RowColumnDefinition, { column: 2, width: 4 }),
            // // Id
            // $(go.TextBlock, { font: '9pt  Segoe UI,sans-serif', stroke: 'black', alignment: go.Spot.Center, background: '#FFD580', desiredSize: new go.Size(20, 20), verticalAlignment: go.Spot.Center, textAlign: 'center'},
            // { row: 0, columnSpan: 4,margin: new go.Margin(1, 0, 1, 0),},
            // new go.Binding('text', 'key', function(v) { return '' + v; })),

            // Title Name
            $(go.TextBlock, { font: '9pt  Segoe UI,sans-serif', stroke: 'black', alignment: go.Spot.Center, width:130, textAlign: 'center'},  // the name
            {
              row: 2, column: 0, columnSpan: 4,
              font: '12pt Segoe UI,sans-serif',
              editable: true, isMultiline: false,
              minSize: new go.Size(10, 16),
              margin: new go.Margin(5, 0, 10, 0),
                alignment: go.Spot.Center
              },
              new go.Binding('text', 'name').makeTwoWay()),

              // Boss Name
              $(go.TextBlock, { font: '9pt  Segoe UI,sans-serif', stroke: 'black', alignment: go.Spot.Center},
              { name: 'Reports_To', row: 1, columnSpan: 4, margin: new go.Margin(1, 0, 1, 0),}, // we include a name so we can access this TextBlock when deleting Nodes/Links
              new go.Binding('text', 'parentObj', function(v) { 
                console.log("parent", v)
                if(v.name){
                  return 'Reports To: ' + v.name;
                }
              })),
              // Roll
            $(go.TextBlock, 'Role', { font: '9pt  Segoe UI,sans-serif', stroke: 'black', background: '#FFD580', width:45, height:18, verticalAlignment: go.Spot.Center, textAlign: 'center'},
              { row: 3, column: 0 }),
            $(go.TextBlock, { font: '9pt  Segoe UI,sans-serif', stroke: 'black', margin: 2, alignment: go.Spot.Center},
              {
                row: 3, column: 1, columnSpan: 4,
                editable: true, isMultiline: false,
                minSize: new go.Size(10, 14),
                margin: new go.Margin(3, 0, 3, 0),
              },
              new go.Binding('text', 'title').makeTwoWay()),

              // Branch
              $(go.TextBlock, 'Branch', { font: '9pt  Segoe UI,sans-serif', stroke: 'black', background: '#FFD580', width:45, height:18, verticalAlignment: go.Spot.Center, textAlign: 'center'},
              { row: 4, column: 0 }),
            $(go.TextBlock, { font: '9pt  Segoe UI,sans-serif', stroke: 'black', margin: 2, alignment: go.Spot.Center},
              {
                row: 4, column: 1, columnSpan: 4,
                editable: true, isMultiline: false,
                minSize: new go.Size(10, 14),
                margin: new go.Margin(3, 0, 3, 0),
              },
              new go.Binding('text', 'branch').makeTwoWay()),

              // RBAC
              $(go.TextBlock, 'RBAC', { font: '9pt  Segoe UI,sans-serif', stroke: 'black', background: '#FFD580', width:45, height:18, verticalAlignment: go.Spot.Center, textAlign: 'center'},
              { row: 5, column: 0 }),
            $(go.TextBlock, { font: '9pt  Segoe UI,sans-serif', stroke: 'black', margin: 2, alignment: go.Spot.Center},
              {
                row: 5, column: 1, columnSpan: 4,
                editable: true, isMultiline: false,
                minSize: new go.Size(10, 14),
                margin: new go.Margin(3, 0, 3, 0),
              },
              new go.Binding('text', 'rbac').makeTwoWay()),
              
            $(go.TextBlock, { font: '9pt  Segoe UI,sans-serif', stroke: 'black' },  // the comments
              {
                row: 6, column: 0, columnSpan: 5,
                font: 'italic 9pt sans-serif',
                wrap: go.TextBlock.WrapFit,
                editable: true,  // by default newlines are allowed
                minSize: new go.Size(10, 14)
              },
              new go.Binding('text', 'comments').makeTwoWay())
          )  // end Table Panel
        ) // end Vertical Panel
      );  // end Node

    this.diagram.model = this.model;

    // when the selection changes, emit event to app-component updating the selected node
    this.diagram.addDiagramListener('ChangedSelection', (e) => {
      const node = this.diagram.selection.first();
      this.nodeClicked.emit(node);
    });

  }

  }
