import { Component, Input, OnInit } from '@angular/core';
import { Route, Router } from '@angular/router';
import { CommonService } from 'src/app/core/services/common.service';
import { OrganizationStructureService } from 'src/app/core/services/organization-structure.service';

@Component({
  selector: 'app-inspector',
  templateUrl: './inspector.component.html',
  styleUrls: ['./inspector.component.scss']
})
export class InspectorComponent implements OnInit {

  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  allNodeArr : any[] = []
  @Input()
  public model: go.Model;

  // To avoid infinite looping
  public _selectedNode: go.Node;
  public data = {
    name: null,
    parent: null,
    title: null,
    branch: null,
    rbac: null,
    _id: null,
  };
  checkPlotFlag: boolean = false;

  ngOnInit(): void {
  }

  @Input()
  get selectedNode() { return this._selectedNode; }
  set selectedNode(node: go.Node) {
    if (node && node != null) {
      this._selectedNode = node;
      this.data.name = this._selectedNode.data.name;
      this.data.parent = this._selectedNode.data.parent;
      this.data.title = this._selectedNode.data.title;
      this.data.branch = this._selectedNode.data.branch;
      this.data.rbac = this._selectedNode.data.rbac;
      this.data._id = this._selectedNode.data._id;
    } else {
      this._selectedNode = null;
    }
  }

  constructor(private organizationService : OrganizationStructureService, private router: Router,
    private commonService : CommonService) { }

  public onCommitForm() {
    this.model.startTransaction();
    this.model.set(this.selectedNode.data, 'name', this.data.name);
    this.model.set(this.selectedNode.data, 'parent', this.data.parent);
    this.model.set(this.selectedNode.data, 'title', this.data.title);
    this.model.set(this.selectedNode.data, 'branch', this.data.branch);
    this.model.set(this.selectedNode.data, 'rbac', this.data.rbac);
    this.model.set(this.selectedNode.data, '_id', this.data._id);
    this.model.commitTransaction();
    this.plotNode(this.model)
  }

  plotNode(model){
    this.checkPlotFlag = true
    console.log(model.Nc)
    // Serialize the data to JSON
    // const jsonData = JSON.stringify(model.Nc);
    // this.allNodeArr.push(model.Nc)
    this.allNodeArr = model.Nc
    console.log(this.allNodeArr)
  }
  
  saveData() {
    const requestBody = {
      userId : this.currentUser.id,
      data : this.allNodeArr.map(node => ({
        key: node.key ? node.key.toString() : "",
        name: node.name ? node.name : "",
        title: node.title ? node.title : "",
        parent: node.parent ? node.parent.toString() : "",
        branch: node.branch ? node.branch : "",
        rbac: node.rbac ? node.rbac : "",
      }))
    }
    this.organizationService.addOrganizationstructure(requestBody, this.currentUser.id).then((res) => {
      console.log(res.data)
      if(res.success){
        this.checkPlotFlag = false
        // this.commonService.notifyDataAdded()
        window.location.reload()
        // this.router.navigate(['/pages/organization-structure'])
      }
    })
  }
}
